-radius: 10px;\n  width: 100%;\n  margin: 0;\n  border-collapse: unset;\n}\ntd:hover {\n  background-color: none !important;\n}\ntable tr:hover:not(:last-child):not(:first-child) {\n  background-color: #eeeeee;\n}\n.table th {\n  border-bottom: 1px solid #e3e3e3;\n  padding: 18px 36px;\n}\n.table td {\n  border-bottom: 1px solid #e3e3e3;\n  padding: 20px 36px;\n  color: #212121;\n  font-size: 14px;\n  line-height: 16px;\n  white-space: nowrap;\n}\n.table .thead {\n  background: #f5f5f5;\n  color: #616161;\n  font-weight: 500;\n  font-size: 14px;\n  line-height: 16px;\n}\n\n.table .action-button {\n  font-weight: 500;\n  font-size: 16px;\n  color: #ffffff;\n  background: #151233;\n  padding: 8px 16px;\n  border-radius: 4px;\n  border: none;\n}\n\n.table .action-button:disabled {\n  opacity: 0.6;\n}\n</style>\n"],"sourceRoot":""}]);
