 \"../../../../domain/model/models\";\nimport { IUseCaseExecutor } from \"../../../../domain/useCase/IUseCaseExecutor\";\nimport { IUser } from \"../../../../../../common/qCloudTemp/runtimeDtoObjects\";\nimport CustomSelect from \"../../components/customSelect.vue\";\nimport { IDialog } from \"../../../../domain/presentation/IDialog\";\n\n\ninterface IOptions { name: string, value: string };\n\nconst router = container.resolve<IRouter>(IRouter);\nconst modelSelector = container.resolve<IModelSelector>(IModelSelector);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\n\nconst workflow = window.workflow;\nconst headers = ref([\n    { title: 'Process Instance ID', field: 'processInstance.processInstanceId' },\n    { title: 'Process ID', field: 'processInstance.processId' },\n    { title: 'Business Key', field: 'processInstance.businessKey' },\n    { title: 'Initiator User ID', field: 'task.user' },\n    { title: 'Initiator Unit ID', field: 'task.unit' },\n    { title: 'Process Start', field: 'processInstance.processStart' },\n]);\nconst data = ref<Array<IProcess>>([]);\nconst listUsers = ref<Array<IUser>>([]);\nconst userOptions = ref<Array<IOptions>>([]);\nconst swimlaneOptions = ref<Array<IOptions>>([]);\nconst usernameInput = ref<string>(\"\");\nconst swimlaneInput = ref<Array<string>>([]);\n\n\nonMounted(async ()=>{\n    executor.execute(\n        async () => {\n            data.value = await workflow.listProcess(); \n            listUsers.value = await workflow.listUsers();\n            convertUserOptions();\n            convertSwimlanes();\n        },\n        { loading: true }\n    )\n})\n\nconst actionsProp = [\n    {\n        name: \"\",\n        class: \"play-button\",\n        icon: \"mdi mdi-play\",\n        disabled: (item: IProcess) => { \n            return !!item.task.taskEnd || false;\n        },\n        onClick: (rowData: IProcess) => {\n            workflow.setProcessToViewModel(rowData);\n            workflow.setIsNewProcess(false);\n            router.push('../processlivepreview/processinstancestarter');\n        }\n    }\n]\n\nfunction newProcess() {\n    const processItems = workflow.processListForStartNewProcess();\n    executor.execute(async () => {\n        dialog.showDialog(defineAsyncComponent(() => import(\"../../components/dialogs/create/createNewProcess.vue\")), \n            { closable: true, width: '604px', height: '300px', title: 'New Instance', closeOnOutClick: true }, {processItems});\n    }, { loading: true });\n    \n}\nfunction convertUserOptions() {\n    let options: Array<IOptions> = [];\n    listUsers.value.forEach((item: IUser) => {\n        options.push({ name: item.name, value: item.id });\n    })\n    userOptions.value = options;\n}\nfunction convertSwimlanes() {\n    swimlaneOptions.value = workflow.listSwimlanes().map((item:string) => { return { name: item, value: item } });\n}\n\nfunction selectUserName(value: string) {\n    usernameInput.value = value;\n}\nfunction selectSwimlanes(value: Array<IOptions>) {\n    swimlaneInput.value = value.map((item: IOptions)=> item.name);\n}\n\n\n</script>\n\n<template>\n    <div class=\"d-flex justify-content-center h-100\" style=\"margin:32px\">\n        <div class=\"card w-100\" style=\"height:fit-content\">\n            <div class=\"card-header\">\n                <h4>Process Instances</h4>\n                <p class=\"mb-0\">Process description will be here, it can be much longer than this text sample.</p>\n                <!-- <button class=\"new-process-button\" @click=\"newProcess\">New Process</button> -->\n            </div>\n            <div class=\"card-body\">\n                <div class=\"d-flex justify-content-between\">\n                    <div class=\"d-flex mb-3\">\n                        <CustomSelect class=\"username\" :options=\"userOptions\" :multiple=\"false\" @input=\"selectUserName\" />\n                        <CustomSelect class=\"username\" :options=\"swimlaneOptions\" :multiple=\"true\"\n                            :defaultMultiple=\"swimlaneInput.map(item => { return { name: item } })\"\n                            multipleSelectedClass=\"swimlane-tag-name\" @input=\"selectSwimlanes\" />\n                        <input class=\"searchProcess\" placeholder=\"Search Processes\" />\n                    </div>\n                    <button class=\"new-process-button\" @click=\"newProcess\">ADD NEW</button>\n                </div>\n                <IDETable :columns=\"headers\" :data=\"data\" :actions=\"actionsProp\" height=\"calc(100vh - 300px)\"/>\n            </div>\n        </div>\n    </div>\n</template>\n<style scoped>\n@import url(\"../../../../styles/IDE3.css\");\n\n.new-process-button {\n    width: 100px;\n    height: 40px;\n    font-weight: 500;\n    font-size: 14px;\n    color: #151233;\n    background: #FFFFFF;\n    padding: 8px 16px;\n    border-radius: 4px;\n    border: 1px solid #151233;\n}\n.new-process-button:hover {\n    background: #151233;\n    color: #FFFFFF;\n}\n.card-header {\n    padding: 20px;\n    background-color: #FFFFFF;\n}\n.card-body {\n    padding: 32px 20px;\n}\n.username {\n    width: 200px;\n    font-size: 15px;\n    height: 40px;\n    line-height: 40px;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 0px 12px;\n    margin-right: 12px;\n}\n.searchProcess {\n    width: 250px;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding-left: 12px;\n}\n.searchProcess:focus-visible {\n    outline: unset;\n}\n</style>\n<style>\n.username .items {\n    color: #212121;\n    border-radius: 8px;\n    border: 1px solid #E3E3E3;\n    overflow: hidden;\n    background-color: #FFFFFF;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    margin-top: 8px;\n}\n\n.username .items .option {\n    color: #212121;\n    padding-left: 12px;\n    line-height: 32px;\n}\n\n.username .items .option:hover {\n    background-color: #EEEEEE;\n}\n\n.username i {\n    font-size: 16px;\n    color: #BCBCBC;\n}\n.swimlane-tag-name {\n    align-items: center;\n    padding: 5px 8px;\n    background: #D0D1E9;\n    display: flex;\n    justify-content: center;\n    border-radius: 20px;\n    margin-right: 4px;\n    font-size: 14px;\n    color: #2F3160;\n    white-space: nowrap;\n    font-weight: 500;\n}\n.play-button {\n    padding: 0 !important;\n    border-radius: 50% !important;\n    color: #151233 !important;\n    background: #EEEEEE !important;\n    width: 40px;\n    height: 40px;\n}\n.play-button:disabled {\n    opacity: 0 !important;\n}\n\n</style>\n\n"],"sourceRoot":""}]);
